body {
    background-color: #f7f7f7;
}

#header {
    height: 67px;
    background-color: #333;
    .contanier {
        height: 67px;
        width: 960px;
        margin: 0 auto;
        .logo {
            margin-left: 4px;
            margin-top: 15px;
        }
        img {
            display: block;
        }
        .name {
            color: white;
            font-size: 20px;
            line-height: 50px;
            font-family: "arial";
            padding-top: 12px;
            margin-left: 25px;
            span {
                color: white;
                font-size: 18px;
                margin-top: -3px;
                float: left;
                margin-left: 20px;
            }
        }
    }
}

.r_content {
    padding-top: 30px;
    position: relative;
    width: 960px;
    height: 750px;
    margin: 0 auto;
    .reg_tab {
        width: 100%;
        height: 35px;
        a {
            width: 146px;
            height: 35px;
            display: inline-block;
            margin-right: 12px;
            position: relative;
            background: #b40707;
        }
        .tab_right {
            background: #ccc;
        }
        span {
            width: 66px;
            display: inline-block;
            height: 35px;
            line-height: 35px;
            color: #fff;
            font-size: 16px;
            text-decoration: none;
            margin-left: 50px;
            margin-right: 30px;
            text-align: center;
        }
        i {
            display: inline-block;
            position: absolute;
            background: url(https://id1.cloud.huawei.com/CAS/up/idmw_rss_44/css/portal/register_rss_EMUIV6/images/reg-img01.png) no-repeat;
            width: 14px;
            height: 22px;
        }
    }
    .phone-icon {
        top: 6px;
        left: 26px;
        background-position: 0 -18px;
    }
    .mail-icon {
        top: 9px;
        left: 16px;
        width: 22px;
        height: 17px;
        background-position: 0 0;
    }
}

.r_content .r {
    position: absolute;
    font-size: 14px;
    right: 0;
    top: 37px;
    a {
        color: #007dff;
    }
}

.reg_detail {
    width: 420px;
    height: 610px;
    padding: 30px 270px;
    background: white;
    .reg_title {
        p {
            text-align: center;
            font-size: 24px;
            color: #333;
        }
        p:nth-of-type(2) {
            font-size: 16px;
            color: #999;
        }
    }
}

.r_content {
    form {
        p {
            width: 420px;
            height: 44px;
            margin-bottom: 30px;
            border-radius: 22px;
            background: rgb(243, 243, 243);
            .matips {
                position: absolute;
                left: 75px;
                bottom: 15px;
                display: none;
            }
            .matips_1 {
                text-align: left;
                display: block;
                position: relative;
                top: 35px;
                padding: 20px 20px 0 40px;
                width: 205px;
                height: 80px;
                border-radius: 5px 5px 0 0;
                border: 1px solid #eee;
                margin-bottom: -16px;
                background: white;
                z-index: 10;
                a {
                    display: block;
                }
            }
            .matips_2 {
                display: block;
                width: 267px;
                height: 120px;
                background: url(https://id1.cloud.huawei.com/CAS/up/idmw_rss_44/css/common/images/auth_tips.png) no-repeat -10px 0;
            }
            .matips_3 {
                position: absolute;
                z-index: 12;
                top: 117px;
                width: 205px;
                height: 23px;
                left: 40px;
                border-bottom: 4px solid rgb(55, 204, 14);
            }
            &:nth-of-type(1) {
                margin-top: 30px;
            }
            select {
                border: 0;
                height: 42px;
                font-size: 20px;
            }
            .tips {
                display: inline-block;
                padding: 7px 16px;
                font-size: 12px;
                color: #ff3320;
            }
        }
    }
    label {
        display: inline-block;
        text-align: center;
        border-radius: 21px 0 0 21px;
        font-size: 14px;
        border: 1px solid #ccc;
        line-height: 42px;
        width: 90px;
        height: 42px;
        padding: 0 16px;
        vertical-align: middle;
    }
    input {
        display: inline-block;
        border: 0;
        border: 1px solid #ccc;
        background: rgb(243, 243, 243);
        border-left: 0;
        border-radius: 0 21px 21px 0;
        width: 295px;
        text-indent: 10px;
        height: 42px;
        vertical-align: middle;
        &::placeholder {
            color: rgb(204, 204, 204);
        }
    }
    .last {
        display: inline-block;
        border: 0;
        border: 1px solid #ccc;
        border-left: 0;
        border-radius: 0 21px 21px 0;
        width: 295px;
        height: 42px;
        line-height: 42px;
        cursor: auto;
        &>span {
            margin-right: 20px;
            display: inline-block;
            line-height: 42px;
            position: relative;
            em {
                font-style: normal;
                font-weight: 400;
            }
            .span1 {
                position: absolute;
                top: 42px;
                left: 0;
                width: 156px;
                height: 200px;
                background: #fff;
                overflow: auto;
                border-radius: 5px;
                z-index: 100;
                display: none;
                box-shadow: 1px 2px 17px 8px rgba(0, 0, 0, 0.03);
                &>span {
                    display: block;
                    width: 120px;
                    height: 15px;
                    padding: 8px 16px;
                    line-height: 15px;
                    border-radius: 5px 0 0 0;
                    ;
                    &:hover {
                        background-color: rgba(0, 125, 255, 0.2);
                    }
                }
            }
            &:nth-of-type(1) {
                margin-left: 15px;
            }
            .bg {
                width: 12px;
                height: 6px;
                background-image: url(https://id1.cloud.huawei.com/CAS/up/idmw_rss_44/css/common/images/triangle_green.png);
                background-size: cover;
                display: inline-block;
                cursor: pointer;
            }
        }
    }
    .button {
        display: block;
        width: 200px;
        height: 40px;
        margin: 30px auto;
        border-radius: 20px;
        cursor: pointer;
        line-height: 40px;
        text-align: center;
        background-color: #b40707;
        border: 0;
        color: #fff;
    }
}

#footer {
    background-color: #f7f7f7;
    margin-top: 50px;
    p {
        color: #999;
        font-size: 14px;
    }
    a {
        color: #999;
        padding: 0 10px;
    }
}

.div_border {
    width: 960px;
    height: 41px;
    margin: 0 auto;
    border-bottom: 1px solid #ccc;
}